/** @module mathlab/planegeometry/pointline */
import math from './../../core/math.js'

/**
 * Line : Ax+By+C=0
 * @private
 * @param  {} A
 * @param  {} B
 * @param  {} C
 */
class Line {
  constructor (A, B, C) {
    this.A = A
    this.B = B
    this.C = C
  }
  toString () {
    return this.A + 'x' + this.B + 'y' + this.C
  }
}
/**
 * 两点间距离
 * @param {pointStart} pointStart 起始点
 * @param {pointEnd} pointEnd 结束点
 * @return {number} 两点间距离
 */
function pointToPoint (pointStart, pointEnd) {
  let dis1 = math.pow(math.subtract(math.bignumber(pointStart.x), math.bignumber(pointEnd.x)), 2)
  let dis2 = math.pow(math.subtract(math.bignumber(pointStart.y), math.bignumber(pointEnd.y)), 2)
  return math.sqrt(math.add(dis1, dis2))
}

/**
 * 根据两点生成直线方程  Ax+By+C=0  标准方程
 * @param {pointStart} pointStart 第一个点 {x:0,y:0}
 * @param {pointEnd} pointEnd 第二个点{x:0,y:0}
 * @return {Object} {a:1,b:1,c:1} Ax+By+C=0
 */
function createLine (pointStart, pointEnd) {
  let a = 0
  let b = 0
  let c = 0
  a = pointEnd.y - pointStart.y
  b = pointStart.x - pointEnd.x
  c = pointEnd.x * pointStart.y - pointStart.x * pointEnd.y
  let line = new Line(a, b, c)
  return line
}
/**
 * 点到线的距离
 * @param {point} point {x:0,y:0}
 * @param {line} line {A:1,B:2,C:3}
 * @return {number} 距离值
 */
function pointToLine (point, line) {
  let lineA = math.multiply(math.bignumber(line.A), math.bignumber(point.x))
  let lineB = math.multiply(math.bignumber(line.B), math.bignumber(point.y))
  let lineC = math.bignumber(line.C)
  let addDis = math.add(math.pow(math.bignumber(line.A), 2), math.pow(math.bignumber(line.B), 2))
  let dis = math.divide(math.add(lineA, lineB, lineC), math.sqrt(addDis))
  return dis - 0
}

/**
 * 获取两条直接相交的距离
 * @param  {line} line0
 * @param  {line} line1
 * @returns number
 */
function getLinesDis (line0, line1) {
  let a0 = math.bignumber(line0.A)
  let b0 = math.bignumber(line0.B)
  let a1 = math.bignumber(line1.A)
  let b1 = math.bignumber(line1.B)
  let dis = math.subtract(math.multiply(a0, b1), math.multiply(a1, b0))
  return dis
}
/**
 * 判断两条直接是否平行
 * @param  {line} line0
 * @param  {line} line1
 * @returns boolean
 */
function isParallelLines (line0, line1) {
  let dis = getLinesDis(line0, line1)
  return (dis === Infinity)
}
/**
 * 两直线的交点，如果是两条平行线返回 Infinity
 * @param  {line} line0 {A:1,B:2,C:3}
 * @param  {line} line1 {A:1,B:2,C:3}
 * @returns {Object} {x:3,y:3},如果是两条平行线返回 Infinity
 */
function linesIntersectPoint (line0, line1) {
  let a0 = math.bignumber(line0.A)
  let b0 = math.bignumber(line0.B)
  let c0 = math.bignumber(line0.C)
  let a1 = math.bignumber(line1.A)
  let b1 = math.bignumber(line1.B)
  let c1 = math.bignumber(line1.C)
  let dis = getLinesDis(line0, line1)
  if (dis === 0) {
    return {}
  } else {
    let x = math.divide(math.subtract(math.multiply(b0, c1), math.multiply(b1, c0)), dis)
    let y = math.divide(math.subtract(math.multiply(a1, c0), math.multiply(a0, c1)), dis)
    return {
      x: x,
      y: y
    }
  }
}
/**
 * 判断点是否在直线上
 * @param  {point} point {x:1, y:1}
 * @param  {line} line {A:1,B:2,C:3}
 * @returns boolean
 */
function pointInLine (point, line) {
  let px = math.bignumber(point.x)
  let py = math.bignumber(point.y)
  let Ax = math.multiply(math.bignumber(line.A), px)
  let By = math.multiply(math.bignumber(line.B), py)
  let ret = math.add(Ax, By, math.bignumber(line.C))
  return (ret === 0)
}

let pointLine = Object.create(null)
pointLine.pointToPoint = pointToPoint
pointLine.createLine = createLine
pointLine.pointToLine = pointToLine
pointLine.linesIntersectPoint = linesIntersectPoint
pointLine.isParallelLines = isParallelLines
pointLine.pointInLine = pointInLine
export default pointLine



