/** @module mathlab/planegeometry/circle */
import math from './../../core/math.js'
import pointLine from './pointline.js'

/**
 *  返回点到圆的距离
 * @param {Object} point {x:0,y:0}
 * @param {Object} circle {center:{x:0,y:0},r:1}
 * @return {number} 返回点到圆的距离,可能是负数
 */
function pointToCircle (point, circle) {
  let xdis = math.pow(math.subtract(math.bignumber(point.x), math.bignumber(circle.center.x)), 2)
  let ydis = math.pow(math.subtract(math.bignumber(point.y), math.bignumber(circle.center.y)), 2)
  let distance = math.sqrt(math.add(xdis, ydis))
  return math.subtract(distance, circle.r)
}
/**
 * 判断点与圆的关系
 * @param {object} point {x:0,y:0}
 * @param {Object} circle {center:{x:0,y:0},r:1}
 * return number 0:在圆上，>0:在圆外，<0:在圆
 */
function pointInCircle (point, circle) {
  let dis = pointToCircle(point, circle)
  return dis
}
/**
 * 线到圆的距离
 * @param  {line} line {A:1,B:2,C:3}
 * @param  {circle} circle {center:{x:0,y:0},r:1}
 * @returns number
 */
function lineToCircle (line, circle) {
  let pointToLineDis = pointLine.pointToLine(circle.center, line)
  return pointToLineDis - circle.r
}
let circle = Object.create(null)
circle.pointInCircle = pointInCircle
circle.pointToCircle = pointToCircle
circle.lineToCircle = lineToCircle
export default circle



