import math from './math.js'
import planegeometry from 'components/planegeometry/index.js'
import utils from 'components/utils/index.js'
let mathlab = Object.create(null)
mathlab.math = math
mathlab.planegeometry = planegeometry
mathlab.utils = utils
if (typeof icCreatePlayer !== 'undefined') {
  if (typeof icCreatePlayer.plugin === 'undefined') {
    icCreatePlayer.plugin = Object.create(null)
  }
  icCreatePlayer.plugin.mathlab = mathlab
}
export default mathlab



