/** @module mathlab/utils/gcdlcm */
/**
 * 获取最大公约数与最小公倍数
 * @param  {Array} arr
 * @returns {Object} {GCD:1,LCM:1}
 */
function getGCDLCM (arr) {
  var max = Math.max.apply(null, arr)
  var min = Math.min.apply(null, arr)
  var GCDLCM = Object.create(null)
  let i
  let j
  let flag = false
  for (j = 0; j < arr.length; j++) {
    if (arr[j] < 1) {
      flag = true
      break
    }
  }
  if (flag) {
    GCDLCM.GCD = -1
    GCDLCM.LCM = -1
    return GCDLCM
  }
  for (i = min; i > 0; i--) {
    flag = false
    for (j = 0; j < arr.length; j++) {
      if (arr[j] % i !== 0) {
        flag = true
        break
      }
    }
    if (!flag) {
      GCDLCM.GCD = i
      break
    }
  }
  var n = 1
  for (j = 0; j < arr.length; j++) {
    n = n * arr[j]
  }
  for (i = max; i <= n; i++) {
    flag = false
    for (j = 0; j < arr.length; j++) {
      if (i % arr[j] !== 0) {
        flag = true
        break
      }
    }
    if (!flag) {
      GCDLCM.LCM = i
      break
    }
  }
  return GCDLCM
}
export default getGCDLCM



